import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import Button from '@material-ui/core/Button'
import Link from './Link'

const editWarning = (
  <DialogContentText>
    <p> OI Wiki </p>
    <p>
      
      <Link to="/intro/htc/"></Link>
      
    </p>
    <p></p>
  </DialogContentText>
)
function EditWarn ({ relativePath, dialogOpen, setDialogOpen }) {
  const editURL = 'https://github.com/OI-wiki/OI-wiki/edit/master/docs/'
  return (
    <Dialog
      open={dialogOpen}
      onClose={() => {
        setDialogOpen(false)
      }}
    >
      <DialogTitle></DialogTitle>
      <DialogContent>{editWarning}</DialogContent>
      <DialogActions>
        <Button
          onClick={() => {
            setDialogOpen(false)
          }}
        >
          
        </Button>
        <Button
          component="a"
          href={editURL + relativePath}
          target="_blank"
          rel="noopener nofollow"
          onClick={() => {
            setDialogOpen(false)
          }}
        >
          
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default EditWarn
